export const USER_SIGN_IN = ()=>{
    return (dispatch:any, getState:any)=>{
        console.log(getState())
        axios.post(API_USER_SIGN_IN_URL, {email: getState().auth.email, password: getState().auth.password})
        .then((res)=>{
            if(res.status === 200){
                const data:any = res.data;
                if(data.success === true){
                    
                }
            }
        })
        .catch((e)=>{
            console.log(e);
        });
    };
};

export const USER_SIGN_UP = () => {
    return (dispatch:any, getState:any)=>{
        console.log(getState())
        var bodyFormData = new FormData();
        bodyFormData.append('email', getState().auth.email);
        bodyFormData.append('username', getState().auth.username);
        bodyFormData.append('password', getState().auth.password);
        bodyFormData.append('firstName', getState().auth.firstName);
        bodyFormData.append('lastName', getState().auth.lastName);
        bodyFormData.append('gender', getState().auth.gender);
        bodyFormData.append('contact', getState().auth.contact);
        bodyFormData.append('address', getState().auth.address);
        bodyFormData.append('userType', getState().auth.userType);
        axios.post(API_USER_SIGN_UP_URL, bodyFormData, {
                headers: {
                    'Content-Type': 'multipart/form-data',
                    'Access-Control-Allow-Origin': API_URL
                }
            }
        )
        .then((res)=>{
            if(res.status === 200){
                const data:any = res.data;
                if(data.success === true){
                    
                }
            }
        })
        .catch((e)=>{
            console.log(e);
        });
    };
};

