import { createSlice } from '@reduxjs/toolkit';

import axios from "axios";

const initialState:any = {
    loading: false,
    notifications: []
};

const appSlice = createSlice({
    name: 'user',
    initialState: initialState,
    reducers: {
        LOADINGSTART: (state) => {
            return {
                ...state,
                loading: true,
            };
        },
        LOADINGEND: (state, action) => {
            return {
                ...state,
                loading: false,
            };
        },
        NOTIFICATIONSADD: (state, action) => {
            return {
                ...state,
                notifications: action.payload,
            };
        },
        NOTIFICATIONSREMOVE: (state, action) => {
            return {
                ...state,
                notifications: action.payload,
            };
        },
    }
});

export const { 
    LOADINGSTART,
    LOADINGEND,
    NOTIFICATIONSADD,
    NOTIFICATIONSREMOVE
} = appSlice.actions

export default appSlice.reducer