import * as React from 'react';
import { useDispatch } from "react-redux";
import { useSelector } from "react-redux";

import { Provider } from 'react-redux';
import store from '../app-store/store';

import { BrowserRouter, Route, Routes } from 'react-router-dom';

import Stack from '@mui/material/Stack';
import CircularProgress from '@mui/material/CircularProgress';

import Backdrop from '@mui/material/Backdrop';

import Snackbar, { SnackbarOrigin } from '@mui/material/Snackbar';
import MuiAlert, { AlertProps } from '@mui/material/Alert';


const Home = React.lazy(() => import('./Home'));
const Login = React.lazy(() => import('../Auth/components/Login'));
const Signup = React.lazy(() => import('../Auth/components/Signup'));
const Logout = React.lazy(() => import('../Auth/components/Logout'));
const ForgotPassword = React.lazy(() => import('../Auth/components/ForgotPassword'));
const ChangePassword = React.lazy(() => import('../Auth/components/ChangePassword'));
const Dashboard = React.lazy(() => import('../Dashboard/components/Dashboard'));
const DashboardInfo = React.lazy(() => import('../Dashboard/components/DashboardInfo/components/DashboardInfo'));
const DashboardProfile = React.lazy(() => import('../Dashboard/components/Profile/components/Profile'));
const DashboardTable = React.lazy(() => import('../Dashboard/components/Table'));

const DashboardLeads = React.lazy(() => import('../Dashboard/components/Leads/components/Leads'));
const DashboardAddLead = React.lazy(() => import('../Dashboard/components/Leads/components/add/AddLead'));
const DashboardEditLead = React.lazy(() => import('../Dashboard/components/Leads/components/edit/EditLead'));
const DashboardListLeads = React.lazy(() => import('../Dashboard/components/Leads/components/list/ListLeads'));

const DashboardAgents = React.lazy(() => import('../Dashboard/components/Agents/components/Agents'));
const DashboardAddAgent = React.lazy(() => import('../Dashboard/components/Agents/components/add/AddAgent'));
const DashboardEditAgent = React.lazy(() => import('../Dashboard/components/Agents/components/edit/EditAgent'));
const DashboardListAgents = React.lazy(() => import('../Dashboard/components/Agents/components/list/ListAgents'));

const DashboardAppointments = React.lazy(() => import('../Dashboard/components/Appointments/components/Appointments'));
const DashboardAddAppointments = React.lazy(() => import('../Dashboard/components/Appointments/components/add/AddAppointment'));
const DashboardEditAppointments = React.lazy(() => import('../Dashboard/components/Appointments/components/edit/EditAppointment'));
const DashboardListAppointments = React.lazy(() => import('../Dashboard/components/Appointments/components/list/ListAppointments'));


export interface State extends SnackbarOrigin {
  open: boolean;
}


function SimpleBackdrop(props:any) {
    const [open, setOpen] = React.useState(true);
    const handleClose = () => {
      setOpen(false);
    };
    const handleToggle = () => {
      setOpen(!open);
    };
  
    return (
      <div>
        <Backdrop
          sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
          open={open}
          onClick={handleClose}
        >
          <CircularProgress color="primary" />
        </Backdrop>
      </div>
    );
}

function Index() {
  return (
    <BrowserRouter>
      <Provider store={store}>
        <React.Suspense fallback={<SimpleBackdrop>Loading...</SimpleBackdrop>}>
          <Routes>
              <Route path="/" element={<Home />}>
              <Route path="" element={<Login />} />
              <Route path="login" element={<Login />} />
              <Route path="logout" element={<Logout />} />
              <Route path="signup" element={<Signup />} />
              <Route path="forgot-password" element={<ForgotPassword />} />
              <Route path="change-password" element={<ChangePassword />} />

              <Route path="dashboard" element={<Dashboard />}>
              
                <Route path="" element={<DashboardInfo />} />
                <Route path="profile" element={<DashboardProfile />} />
                <Route path="table" element={<DashboardTable />} />

                <Route path="leads">
                  <Route path="add" element={<DashboardAddLead />} />
                  <Route path="edit" element={<DashboardEditLead />} />
                  <Route path="list" element={<DashboardListLeads />} />
                </Route>

                <Route path="agents">
                  <Route path="add" element={<DashboardAddAgent />} />
                  <Route path="edit" element={<DashboardEditAgent />} />
                  <Route path="list" element={<DashboardListAgents />} />
                </Route>

                <Route path="appointments">
                  <Route path="add" element={<DashboardAddAppointments />} />
                  <Route path="edit" element={<DashboardEditAppointments />} />
                  <Route path="list" element={<DashboardListAppointments />} />
                </Route>
                
              </Route>
              <Route
                  path="*"
                  element={
                  <main style={{ padding: "1rem" }}>
                      <p>There's nothing here!</p>
                  </main>
                  }
              />
              </Route>
          </Routes>
        </React.Suspense>
      </Provider>
    </BrowserRouter>
  );
}

export default Index;
